import { api, setAuthToken } from "../../utils";
import { showAlertMessage } from "./alerts";

// Define Actions
const REGISTER_SUCCESS = "users/REGISTER_SUCCESS";
const REGISTER_FAIL = "users/REGISTER_FAIL";
const USER_LOADED = "users/USER_LOADED";
const USER_ERROR = "users/USER_ERROR";
const LOGIN_SUCCESS = "users/LOGIN_SUCCESS";
const LOGIN_FAIL = "users/LOGIN_FAIL";
const LOGOUT = "users/LOGOUT";

export const loadUser = () => async (dispatch) => {
  try {
    const res = await api.get("/users");
    dispatch({
      type: USER_LOADED,
      payload: res.data,
    });
  } catch (error) {
    dispatch({
      type: USER_ERROR,
    });
  }
};

// Creator Action
// name, email, password, confirmPassword => Form Data
export function register(formData) {
  return async function registerThunk(dispatch) {
    try {
      // Call API /users/register
      const res = await api.post("/users/register", formData);
      dispatch({
        type: REGISTER_SUCCESS,
        payload: res.data, // data come from API
      });
      dispatch(loadUser());
    } catch (error) {
      // Optional chaining to safely access errors if they exist
      const errors = error.response?.data?.errors;
      if (errors) {
        errors.forEach((error) => {
          // we will alert module when happen error
          // error.msg(msg Key),  "error" (type of alert)
          dispatch(showAlertMessage(error.msg, "error"));
        });
        dispatch({
          type: REGISTER_FAIL,
        });
      }
    }
  };
}
export function login(email, password) {
  return async function loginThunk(dispatch) {
    try {
      const res = await api.post("/users/login", { email, password });
      dispatch({
        type: LOGIN_SUCCESS,
        payload: res.data,
      });
      dispatch(loadUser());
    } catch (error) {
      const errors = error.response?.data?.errors;
      if (errors) {
        errors.forEach((error) => {
          dispatch(showAlertMessage(error.msg, "error"));
        });
        dispatch({
          type: LOGIN_FAIL,
        });
      }
    }
  };
}

export const logout = () => async (dispatch) => {
  dispatch({
    type: LOGOUT,
  });
};

const initialState = {
  token: localStorage.getItem("token"),
  isAuthenticated: null, // // user authenticated or not
  loading: true, // component loaded or not
  user: null, // info about user (we get info from server)
};
export default function reducer(state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case USER_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        loading: false,
        user: payload,
      };
    case REGISTER_SUCCESS:
    case LOGIN_SUCCESS:
      // set token come from server
      setAuthToken(payload.token);
      return {
        ...state,
        token: payload.token,
        isAuthenticated: true,
        loading: false,
      };
    case REGISTER_FAIL:
    case LOGIN_FAIL:
      setAuthToken();
      return {
        ...state,
        token: null,
        isAuthenticated: false,
        loading: false,
      };
    case USER_ERROR:
    case LOGOUT:
      setAuthToken();
      return {
        ...state,
        token: null,
        isAuthenticated: false,
        loading: false,
        user: null,
      };

    default:
      return state;
  }
}
